import React, { Component } from 'react'

//import the SVGs
import SLetter  from '../icons/sLetter'
import MLetter  from '../icons/mLetter'

export default class IconLayout extends Component {
    state = {
        bodyWidth: 0,
        bodyHeight: 0,
        logoCount: 35,
    }

    componentDidMount() {
        setTimeout(()=>{
            this.setDimentions()
        }, 200)
    }

    setDimentions = () => {
        const bodyWidth = document.body.clientWidth

        //height 
        const windowH = window.innerHeight
        const bodyH = document.body.clientHeight
        let bodyHeight =  ( windowH > bodyH ) ? windowH : bodyH
        this.setState({
            bodyWidth,
            bodyHeight
        })
    }

    render(){   
        
        const { bodyHeight, bodyWidth, logoCount } = this.state
        const styles = {
            height:`${bodyHeight}px`
        }
        let logos = []

        for (let i = 0; i < logoCount; i++) {
            const randPosX = Math.floor((Math.random()*bodyWidth));
            const randPosY = Math.floor((Math.random()*bodyHeight));
            if( (i % 2) === 0) {
                logos.push(
                    < SLetter key={i} styles={{position: 'absolute', top: `${randPosY}px`, left: `${randPosX}px`}} />
                )
            }else {
                logos.push(
                < MLetter key={i} styles={{position: 'absolute', top: `${randPosY}px`, left: `${randPosX}px`}} />
                )
            }
        }

        return(
        <div className="logo-container" style={styles}>
            {logos}
        </div>
        )
    }
}