import React, { Component } from 'react'

//components
import WorkComponent from './WorkComponent'
//pojects
import { workProjects } from '../../files/works'
//scss 
import '../styles/work2.scss'

export default class WorkSceneTwo extends Component {

    state = {
        workProjects: ''
    }

    componentDidMount(){
        this.setState({
            workProjects
        })
    }
    render() {
        const { workProjects } = this.state
        let renderProjects = []

        for (let proj in workProjects) {
            let project = workProjects[proj]
            renderProjects.push(
                <WorkComponent project={project} key={proj}/>
            )
        }

        return (
        <div className="container">
            <div className="work2-container">

                <div className="work-flex-container">
                {renderProjects}
                </div>

            </div>
        </div>
        )
    }
}